
// 'use client'; // This line is removed from the top of the file

import type { Metadata } from 'next';
import { Geist, Geist_Mono } from 'next/font/google';
import './globals.css';
import { Toaster } from '@/components/ui/toaster';
import { LoadingProvider } from '@/contexts/loading-context';
import { ThemeProvider } from '@/components/theme-provider';
import { SidebarProvider } from '@/components/ui/sidebar';

// IMPORTS for AppShell (Client Component part)
import { useEffect, useState } from 'react';
import { useRouter, usePathname } from 'next/navigation';
import { useLoading } from '@/contexts/loading-context';
import LoadingProgressBar from '@/components/loading-progress-bar';
import FooterComponent from '@/components/footer';
import {
  Sidebar,
  SidebarHeader,
  SidebarContent,
  SidebarMenu,
  SidebarMenuItem,
  SidebarMenuButton,
  SidebarInset,
  SidebarTrigger,
  SidebarFooter,
} from '@/components/ui/sidebar';
import LoadingLink from '@/components/loading-link';
import { LayoutDashboard, PlusCircle, LogOut, Home, LogIn, Loader2, ListFilter, Settings, Users as UsersIcon, ListChecks } from 'lucide-react';
import { getSessionUser } from '@/lib/actions';
import type { User as UserType } from '@/lib/types';
import { Button } from '@/components/ui/button';
import UserAvatar from '@/components/user-avatar';
import { Badge } from '@/components/ui/badge';
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import { ThemeToggle } from '@/components/theme-toggle';
import Header from '@/components/header';

const geistSans = Geist({
  variable: '--font-geist-sans',
  subsets: ['latin'],
});

const geistMono = Geist_Mono({
  variable: '--font-geist-mono',
  subsets: ['latin'],
});

// Metadata for the entire application
export const metadata: Metadata = {
  title: 'VRCub 活动中心 - 我的世界活动平台',
  description: '轻松发现、加入和创建《我的世界》活动！',
  icons: {
    icon: '/favicon.ico?favicon.56766c03.ico', sizes: "48x48", type: "image/x-icon"
  },
};

// This AppShell component uses client-side hooks and renders client components.
// It is rendered inside all necessary providers.
function AppShell({ children }: { children: React.ReactNode }) {
  const [currentUser, setCurrentUser] = useState<UserType | null>(null);
  const [isLoadingUser, setIsLoadingUser] = useState(true);
  const { startLoading } = useLoading(); // Called within AppShell, which is inside LoadingProvider
  const router = useRouter();
  const pathname = usePathname();

  useEffect(() => {
    async function fetchUser() {
      setIsLoadingUser(true);
      try {
        const user = await getSessionUser();
        setCurrentUser(user);
      } catch (error) {
        console.error("Failed to fetch session user for AppShell:", error);
        setCurrentUser(null);
      } finally {
        setIsLoadingUser(false);
      }
    }
    fetchUser();
  }, [pathname]);

  const handleLogout = async (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    startLoading();
    event.currentTarget.submit();
  };

  const handleLoginRedirect = () => {
    startLoading();
    router.push('/login');
  };

  const GlobalLogoutForm = () => (
    <form id="global-logout-form" action="/api/auth/logout" method="POST" onSubmit={handleLogout} className="hidden"></form>
  );

  return (
    <>
      <LoadingProgressBar />
      <GlobalLogoutForm />
      <Header user={currentUser} /> {/* Header is inside AppShell, which is inside SidebarProvider */}
      
      <div className="flex flex-1 min-h-[calc(100vh-4rem)]"> {/* Adjust min-height if header height changes */}
        <Sidebar
          collapsible="icon"
          variant="sidebar"
          className="border-r bg-sidebar text-sidebar-foreground group hidden md:flex md:flex-col h-[calc(100vh-4rem)] sticky top-16 z-30" // Adjusted top and height
          style={{ '--sidebar-width': '260px' } as React.CSSProperties}
        >
          <SidebarHeader className="p-4 flex items-center gap-3 border-b">
            <LoadingLink href="/" className="flex items-center space-x-2">
              {/* Gem icon was removed as per user request */}
              <span className="font-bold sm:inline-block text-lg group-data-[collapsible=icon]:hidden">
                VRCub 活动中心
              </span>
              <Badge variant="outline" className="ml-2 border-orange-500 text-orange-600 dark:border-orange-400 dark:text-orange-400 text-xs px-1.5 py-0.5 group-data-[collapsible=icon]:hidden">Beta</Badge>
            </LoadingLink>
          </SidebarHeader>

          <SidebarContent className="p-2 flex-grow">
            <SidebarMenu>
              <SidebarMenuItem>
                <SidebarMenuButton asChild isActive={pathname === '/'}>
                  <LoadingLink href="/">
                    <Home />
                    <span className="group-data-[collapsible=icon]:hidden">首页</span>
                  </LoadingLink>
                </SidebarMenuButton>
              </SidebarMenuItem>
              {!isLoadingUser && currentUser && (
                <SidebarMenuItem>
                  <SidebarMenuButton asChild isActive={pathname.startsWith('/dashboard')}>
                    <LoadingLink href="/dashboard">
                      <LayoutDashboard />
                      <span className="group-data-[collapsible=icon]:hidden">仪表盘</span>
                    </LoadingLink>
                  </SidebarMenuButton>
                </SidebarMenuItem>
              )}
              {!isLoadingUser && currentUser?.op === 1 && (
                <>
                  <SidebarMenuItem>
                    <SidebarMenuButton asChild isActive={pathname.startsWith('/admin/create-event')}>
                      <LoadingLink href="/admin/create-event">
                        <PlusCircle />
                        <span className="group-data-[collapsible=icon]:hidden">创建活动</span>
                      </LoadingLink>
                    </SidebarMenuButton>
                  </SidebarMenuItem>
                  <SidebarMenuItem>
                    <SidebarMenuButton asChild isActive={pathname.startsWith('/admin/manage-events')}>
                      <LoadingLink href="/admin/manage-events">
                        <ListFilter />
                        <span className="group-data-[collapsible=icon]:hidden">管理活动</span>
                      </LoadingLink>
                    </SidebarMenuButton>
                  </SidebarMenuItem>
                   <SidebarMenuItem>
                    <SidebarMenuButton asChild isActive={pathname.startsWith('/admin/manage-users')}>
                      <LoadingLink href="/admin/manage-users">
                        <UsersIcon />
                        <span className="group-data-[collapsible=icon]:hidden">用户管理</span>
                      </LoadingLink>
                    </SidebarMenuButton>
                  </SidebarMenuItem>
                </>
              )}
            </SidebarMenu>
          </SidebarContent>

          <SidebarFooter className="p-3 border-t mt-auto space-y-2">
            {!isLoadingUser && currentUser ? (
                <DropdownMenu>
                <DropdownMenuTrigger asChild>
                    <Button variant="ghost" className="w-full justify-start group-data-[collapsible=icon]:justify-center group-data-[collapsible=icon]:px-0 h-auto py-2 px-2">
                        <UserAvatar user={currentUser} className="h-9 w-9 group-data-[collapsible=icon]:mr-0 mr-2" />
                        <div className="group-data-[collapsible=icon]:hidden text-left">
                        <p className="text-sm font-medium truncate">{currentUser.username}</p>
                        <p className="text-xs text-muted-foreground">{currentUser.op === 1 ? '管理员' : '用户'}</p>
                        </div>
                    </Button>
                </DropdownMenuTrigger>
                <DropdownMenuContent className="w-56 mb-2 group-data-[collapsible=icon]:ml-12" align="end" side="top" forceMount>
                    <DropdownMenuLabel className="font-normal">
                    <div className="flex flex-col space-y-1">
                        <p className="text-sm font-medium leading-none">{currentUser.username}</p>
                        <p className="text-xs leading-none text-muted-foreground">
                        ID: {currentUser.id}
                        </p>
                    </div>
                    </DropdownMenuLabel>
                    <DropdownMenuSeparator />
                    <DropdownMenuItem asChild>
                        <Button type="submit" form="global-logout-form" variant="ghost" className="w-full justify-start p-2 h-auto text-sm relative cursor-default select-none items-center gap-2 outline-none transition-colors focus:bg-accent focus:text-accent-foreground data-[disabled]:pointer-events-none data-[disabled]:opacity-50">
                            <LogOut className="mr-2 h-4 w-4" />
                            <span>退出登录</span>
                        </Button>
                    </DropdownMenuItem>
                </DropdownMenuContent>
                </DropdownMenu>
            ) : !isLoadingUser && !currentUser ? (
                <Button variant="default" size="sm" onClick={handleLoginRedirect} className="w-full justify-start group-data-[collapsible=icon]:justify-center group-data-[collapsible=icon]:px-0">
                <LogIn className="h-4 w-4 group-data-[collapsible=icon]:mr-0 mr-2" />
                <span className="group-data-[collapsible=icon]:hidden">登录/注册</span>
                </Button>
            ) : (
                <div className="flex items-center justify-center h-10 w-full">
                <Loader2 className="h-5 w-5 animate-spin text-muted-foreground" />
                </div>
            )}
          </SidebarFooter>
        </Sidebar>

        <SidebarInset className="flex-1 flex flex-col overflow-y-auto mt-16"> {/* mt-16 to push content below header */}
          <main className="flex-grow w-full p-4 md:p-6 overflow-y-auto">
            {children}
          </main>
          <FooterComponent />
        </SidebarInset>
      </div>
      <Toaster />
    </>
  );
}

export default function RootLayout({
  children,
}: Readonly<{
  children: React.ReactNode;
}>) {
  // RootLayout is a Server Component. It does not use client hooks.
  // It sets up providers and renders AppShell which contains client logic.
  return (
    <html lang="zh-CN" suppressHydrationWarning>
      <head>
        {/* Favicon link */}
        <link rel="icon" href="/favicon.ico?favicon.56766c03.ico" sizes="48x48" type="image/x-icon"/>
      </head>
      <body
        className={`${geistSans.variable} ${geistMono.variable} font-sans antialiased flex flex-col min-h-screen bg-background text-foreground`}
      >
        <ThemeProvider
            attribute="class"
            defaultTheme="system"
            enableSystem
            disableTransitionOnChange
          >
          <LoadingProvider>
            <SidebarProvider defaultOpen> {/* SidebarProvider wraps AppShell */}
              <AppShell>{children}</AppShell>
            </SidebarProvider>
          </LoadingProvider>
        </ThemeProvider>
      </body>
    </html>
  );
}

    